import React from "react";
import "./benevole-page-style.scss";
import { Link, graphql, useStaticQuery } from "gatsby";
import HeroSection from "../../molecules/HeroSection/HeroSection";
import WrapperContainer from "../container/WrapperContainer";
import { RegisterForm } from "../../organisms/registerForm/RegisterForm";
import InputType from "../../molecules/InputType/InputType";
const BenevolePageWrapper = () => {
  const { benevoleImage } = useStaticQuery(graphql`
    query benevoleQuery {
      benevoleImage: file(relativePath: { eq: "people.jpg" }) {
        childImageSharp {
          fluid(quality: 90, maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  return (
    <WrapperContainer>
      <HeroSection image={benevoleImage.childImageSharp.fluid}>
        <div className="heroInfo d-center">
          <h1 className="heading-standard hero-title-center text-white">je deviens benevole</h1>
        </div>
      </HeroSection>
      <div className="container px-large">
        <p className="p-vertical">
          Vous souhaitez nous apporter votre expertise sur un sujet qui vous tient à cœur ? Vous souhaitez rejoindre notre équipe de bénévoles parce que nos combats sont aussi les vôtres ?
        </p>

        <p className="p-vertical">Alors, nous vous remercions de déposer ici vos coordonnées. Ensemble, nous étudierons comment notre engagement peut devenir le vôtre.</p>
      </div>
      <div className="section-register  ">
        <div className="container">
          <RegisterForm>
            <InputType tag="textarea" type="textarea" />
          </RegisterForm>
        </div>
      </div>
    </WrapperContainer>
  );
};
export default BenevolePageWrapper;
